<template>
  <div class="personnal-detail-wrap">
    <div class="personnal-detail-wrap-title"></div>
    <common-table
      :columns="tableColumns"
      :datas="tableData"
      :pagger="false"
      th-class="table-innerhead"
      border
    >
      <template slot="oprate" slot-scope="scope">
        <el-button type="primary" size="mini" @click="look(scope.row)"></el-button>
      </template>
    </common-table>
    <bottom-pager :total="total" @change="pageChange"/>
    <div class="personnal-detail-wrap-title"></div>
    <div class="personnal-detail-wrap-selector">
      <el-row :gutter="20">
        <el-col :span="8">
          <span class="label"></span>
          <el-select size="mini" v-model="subject">
            <el-option
              v-for="item in subjects"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          <span class="label"></span>
          <el-select size="mini" v-model="date">
            <el-option
              v-for="item in dates"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="8">
          <span class="label"></span>
          <el-select size="mini" v-model="type">
            <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
      </el-row>
    </div>
    <div id="personnalStuGradeChart" style="width:100%;height: 475px"></div>
  </div>
</template>
<script>
import "echarts/lib/chart/line";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import commonTable from "../../../components/commonTable.vue";
import bottomPager from "../../../components/personal/bottomPager.vue";
import api from "../../../utils/api.js";
let echarts = require("echarts/lib/echarts");
export default {
  components: {
    commonTable,
    bottomPager
  },
  data() {
    return {
      tableColumns: [
        {
          prop: "subject",
          label: ""
        },
        {
          prop: "date",
          label: ""
        },
        {
          prop: "type",
          label: ""
        },
        {
          prop: "grade",
          label: ""
        },
        {
          prop: "oprate",
          label: "",
          slot: true,
          width: 180
        }
      ],
      tableData: [
        {
          id:"123",
          subject: "",
          date: "2019-05-12 14:00",
          type: "",
          grade: "83"
        }
      ],
      total: 1,
      subject: 0,
      subjects: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ],
      date: 0,
      dates: [
        {
          value: 0,
          label: ""
        }
      ],
      type: 0,
      types: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: ""
        }
      ]
    };
  },
  mounted() {
    this.drawChart();
  },
  methods: {
    look(row) {
      this.$router.push(`/main/studentSpace/grade/detail/${row.id}`);
    },
    pageChange(currentPage) {},
    drawChart() {
      let option = {
        color: api.colors,
        grid: {
          top: "9%",
          bottom: "9%",
          left: "5%",
          right: "5%"
        },
        legend: {
          show: true,
          type: "plain",
          right: 0
        },
        tooltip: {
          trigger: "axis",
          label: {
            show: true
          }
        },
        xAxis: {
          boundaryGap: false,
          axisLine: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: "dashed",
              color: "rgba(33,148,246,0.2)"
            }
          },
          axisTick: {
            show: false,
            alignWithLabel: true
          },
          interval: 0,
          min: 0,
          data: [
            "2019-05-05\n08:50",
            "2019-05-06\n14:00",
            "2019-05-07\n09:00",
            "2019-05-08\n14:00",
            "2019-05-09\n15:00",
            "2019-05-10\n14:00",
            "2019-05-11\n18:00",
            "2019-05-12\n14:00",
            "2019-05-13\n15:00",
            "2019-05-14\n09:00"
          ]
        },
        yAxis: {
          axisLine: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              type: "dashed",
              color: "rgba(33,148,246,0.2)"
            }
          },
          axisTick: {
            show: false
          }
        },
        series: [
          {
            name: "",
            type: "line",
            symbol: "circle",
            symbolSize: 7,
            lineStyle: {
              shadowBlur: 12,
              shadowColor: "rgb(33,148,246,0.9)",
              shadowOffsetX: 1,
              shadowOffsetY: 1
            },
            itemStyle: {
              borderWidth: 1,
              borderColor: "#FFF"
            },
            data: [60, 66, 72, 77, 82, 86, 88, 92, 96, 100]
          }
        ]
      };
      let chart = echarts.init(
        document.getElementById("personnalStuGradeChart")
      );
      chart.setOption(option);
    }
  }
};
</script>
<style lang="less">
.personnal-detail-wrap {
  &-selector {
    width: 650px;
    font-size: 0.9rem;
    user-select: none;
    .el-select {
      width: 125px;
    }
  }
}
</style>



